import React from 'react'
import { useHistory } from "react-router-dom";

export default function NoView() {
  let history = useHistory();

  const goHome = (e) => {
    e.preventDefault();
    history.push("/search");
  }

  return (
    <div>
        <p>Dont know Rick, this route looks fake to me!</p>
        <button onClick={(e) => goHome(e)}>Go back</button>
    </div>
  )
}
